___CSS_LOADER_EXPORT___.push([module.id, `:root {
  color-scheme: dark;
}
body {
  background-color: #303030;
  width: 24rem;
  height: 24rem;
}

#popup-container {
  display: flex;
  flex-direction: column;
}

#entire-top-bar {
  padding-top: 0.5rem;
  padding-bottom: 0.8rem;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #d1d1cf;
}

.top-bar-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.5rem;
}

.header-text {
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  color: #d1d1cf;
  margin-right: 0.2rem;
}

.top-bar-icon {
  width: 32px;
  height: 32px;
}

/* on off slider */
.slider-container {
  width: 34px;
  height: 21px;
  position: relative;
}

.slider-checkbox {
  opacity: 0;
  width: 0;
  height: 0;
  position: absolute;
}

.slider-switch {
  width: 100%;
  height: 100%;
  display: block;
  background-color: #484847;
  border-radius: 11px;
  cursor: pointer;
  transition: all 0.2s ease-out;
}

.slider-slider {
  width: 18px;
  height: 18px;
  position: absolute;
  left: calc(50% - 18px / 2 - 7px);
  top: calc(50% - 18px / 2);
  border-radius: 50%;
  background: #767675;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.15), 0px 2px 1px rgba(0, 0, 0, 0.06);
  transition: all 0.2s ease-out;
  cursor: pointer;
}

.slider-checkbox:checked + .slider-switch {
  background-color: #34c759;
}

.slider-checkbox:checked + .slider-switch .slider-slider {
  left: calc(50% - 18px / 2 + 7px);
  top: calc(50% - 18px / 2);
}

/* Settings Button */
#settings-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

#settings-icon img {
  width: 100%;
  height: 100%;
  display: block;
}

.button-hover-effect {
  padding: 6px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}
.button-hover-effect:hover {
  background-color: #484847;
}

/* middle */
#middle-section {
  max-height: 20rem;
  overflow-y: auto;
  overflow-x: hidden;
}

.add-new-blocked-site {
  padding: 0.5rem;
  height: 4rem;
}

.cont {
  margin: 10px;
}
.blocked-add-icon {
  width: 44px;
  height: 44px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.blocked-card-inside {
  gap: 1.5rem;
}

.blocked-card-outline {
  margin: 5px 10px 5px 10px;
  display: flex;
}

.blocked-card-add {
  justify-content: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.blocked-site-img {
  width: 52px;
  height: 52px;
}

.blocked-site-text-container {
  padding-top: 0.5rem;
  align-self: flex-start;
  display: flex;
  flex-direction: column;
}

.blocked-site-url {
  font-weight: 400;
  font-size: 18px;
  color: #d1d1cf;
  padding-bottom: 0.2rem;
}

.blocked-site-under-text {
  font-size: 12px;
  color: #7e7e74;
}
/* Bottom bar */
#bottom-bar {
  padding-top: 0.5rem;
  padding-bottom: 0.8rem;
  height: 32px;
}

/* Popup for adding new sites */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background-color: #303030;
  padding: 20px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: relative;
  min-width: 200px;
}

.popup-content button {
  padding: 10px;
  background-color: #484847;
  color: #d1d1cf;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.cross-button {
  display: flex;
  justify-content: center;
}

.cross-image {
  padding-top: 5px;
  padding-bottom: 5px;
  height: 20px;
  width: 20px;
}
